import { Injectable } from '@angular/core';
import 'rxjs/add/operator/map';
import { Http} from '@angular/http';
import {Config} from '../config'
@Injectable({
    providedIn: 'root'
  })
  export class CommentsService {
    constructor(public http: Http) {

     }
     getCampaignComments = (campaignId) =>{
      
     }
     sendComment = (campaignId,comment) =>{
      if(localStorage["exUserProfile"]){
        let user = JSON.parse(localStorage["exUserProfile"]);
      let obj = { "msg": comment, "externalUsersId": user.id }
      return  this.http.post(Config.END_POINT_URL +'api/campaigns/'+ campaignId +'/campaignComments',obj).map(res =>{
         return res.json();
        })
      }
     }
     replyComment = (campaignId,comment,commentId) =>{
       if(localStorage["exUserProfile"]){
         let user = JSON.parse(localStorage["exUserProfile"]);
        let obj = { "msg": comment, "externalUsersId":user.id ,"parent": commentId}
        return  this.http.post(Config.END_POINT_URL +'api/campaigns/'+ campaignId +'/campaignComments',obj).map(res =>{
           return res.json();
          })
       }
      
     }
  }