import {Component, OnInit, Output, EventEmitter, Input} from '@angular/core';

@Component({selector: 'fb-confirmation-model', templateUrl: './fb-confirmation-model.component.html', styleUrls: ['./fb-confirmation-model.component.less']})
export class FbConfirmationModelComponent implements OnInit {
  comment : string;
  @Output()submit : EventEmitter < object > = new EventEmitter < object > ();
  @Output()closePopUP : EventEmitter < boolean > = new EventEmitter < boolean > ();
  @Input()id : EventEmitter < number > = new EventEmitter < number > ();
  constructor() {}
  isModelClose:boolean;
  ngOnInit() {
    this.isModelClose = false;
  }
  closeDismissPopUP() {
    this.isModelClose = true;
    this
      .closePopUP
      .emit(true)
  }
  formSubmit(comment) {
    if (comment) {
      this.isModelClose = true;
      let obj = {
        "id": this.id,
        "msg": comment
      }
      this
        .submit
        .emit(obj)
    }
  }

}
