import {
  Component,
  OnInit,
  OnChanges,
  Input,
  OnDestroy,
  Output,
  EventEmitter
} from '@angular/core';
declare var $ : any;
@Component({selector: 'pinterest', templateUrl: './pinterest.component.html', styleUrls: ['./pinterest.component.less']})
export class PinterestComponent implements OnInit,
OnChanges,
OnDestroy {
  @Input()piPostData : any;
  // @Input()hideTags : any;
  // @Output()piApprovedPost : EventEmitter < number > = new EventEmitter < number > ();
  // @Output()piRejectPost : EventEmitter < number > = new EventEmitter < number > ();
  // @Input()totalPost : number;
  // @Input()totalReviewPost : number;

  lastSlide : number = 0;
  constructor() {}

  ngOnInit() {
    $('.carousel').carousel({interval: false});
  }
  ngOnChanges() {}
  ngOnDestroy() {}
  imageSlider(i) {
    if (this.lastSlide >= 0 && this.lastSlide < i) {
      $('.carousel').carousel("next");
      this.lastSlide = i;
    } else if (this.lastSlide >= 0 && this.lastSlide > i) {
      $('.carousel').carousel("prev");
      this.lastSlide = i;
    }

  }
}
