import {
  Component,
  OnInit,
  Input,
  OnDestroy,
  Output,
  EventEmitter
} from '@angular/core';
import { PostViewModel } from '../model/post-view.model';
declare var $ : any;
@Component({selector: 'twitter', templateUrl: './twitter.component.html', styleUrls: ['./twitter.component.less']})
export class TwitterComponent implements OnInit,
OnDestroy {

  @Input()twPostData : PostViewModel;
  // @Input()hideTags : any;
  // @Output()twApprovedPost : EventEmitter < number > = new EventEmitter < number > ();
  // @Output()twRejectPost : EventEmitter < number > = new EventEmitter < number > ();
  // @Input()totalPost : number;
  // @Input()totalReviewPost : number;
  lastSlide : number = 0;
  constructor() {}

  ngOnInit() {
    $('.carousel').carousel({interval: false});

    setInterval(() => {
      $(".tooltiptext").css({"visibility": "hidden"})
    }, 5000)
  }
  ngOnChanges() {}
  ngOnDestroy() {}

  imageSlider(i) {
    if (this.lastSlide >= 0 && this.lastSlide < i) {
      $('.carousel').carousel("next");
      this.lastSlide = i;
    } else if (this.lastSlide >= 0 && this.lastSlide > i) {
      $('.carousel').carousel("prev");
      this.lastSlide = i;
    }

  }
}
