import { Component, OnInit, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'compose-component',
  templateUrl: './compose-component.component.html',
  styleUrls: ['./compose-component.component.less']
})
export class ComposeComponentComponent implements OnInit {
  @Output() newComment:EventEmitter<string> = new EventEmitter<string>();
  message:string;
  constructor() { }

  ngOnInit() {
  }
  sendMessage(){
    if(this.message){
      this.newComment.emit(this.message)
      this.message = "";
    }

  }
}
