import {Component, OnInit, Input} from '@angular/core';
declare var $ : any;
@Component({selector: 'ig-post-image', templateUrl: './post-image.component.html', styleUrls: ['./post-image.component.less']})
export class IgPostImageComponent implements OnInit {
  @Input()postImages : Array < any >;
  lastSlide : number = -1;
  constructor() {}

  ngOnInit() {
    $('.carousel').carousel({interval: false});
  }
  showTags(id) {

    for (let j = 0; j < this.postImages.length; j++) {
      if (id === this.postImages[j].id) {
        this.postImages[j].isShowTags = !this.postImages[j].isShowTags;
      }
    }
  }
  tagsHide() {
    for (let j = 0; j < this.postImages.length; j++) {
      this.postImages[j].isShowTags = false;

    }
  }
  imageSlider(i) {
    if (this.lastSlide >= 0 && this.lastSlide < i) {
      $('.carousel').carousel("next");
      this.lastSlide = i;
    } else if (this.lastSlide >= 0 && this.lastSlide > i) {
      $('.carousel').carousel("prev");
      this.lastSlide = i;
    }

  }
}
