import {Component, OnInit, Input} from '@angular/core';
declare var $ : any;
@Component({selector: 'ig-post-video', templateUrl: './post-video.component.html', styleUrls: ['./post-video.component.less']})
export class IgPostVideoComponent implements OnInit {
  @Input()videos : Array < string >;
  lastSlide : number = 0;
  constructor() {}

  ngOnInit() {
    $('.carousel').carousel({interval: false});
  }
  imageSlider(i) {
    if (this.lastSlide >= 0 && this.lastSlide < i) {
      $('.carousel').carousel("next");
      this.lastSlide = i;
    } else if (this.lastSlide >= 0 && this.lastSlide > i) {
      $('.carousel').carousel("prev");
      this.lastSlide = i;
    }

  }
}
