import {Component, OnInit, Output, EventEmitter, Input} from '@angular/core';

@Component({selector: 'pi-confirmation-model', templateUrl: './pi-confirmation-model.component.html', styleUrls: ['./pi-confirmation-model.component.less']})
export class PiConfirmationModelComponent implements OnInit {
  comment : string;
  @Output()submit : EventEmitter < object > = new EventEmitter < object > ();
  @Output()closePopUP : EventEmitter < boolean > = new EventEmitter < boolean > ();
  @Input() id: EventEmitter < number > = new EventEmitter < number > ();
  isModelClose:boolean;
  constructor() {}

  ngOnInit() {
    this.isModelClose = false;
  }
  closeDismissPopUP() {
    this
      .closePopUP
      .emit(true);
      this.isModelClose = true;
  }
  formSubmit(comment) {
    if (comment) {
      this.isModelClose = true;
      let obj = {
        "id": this.id,
        "msg": comment
      }
      this
        .submit
        .emit(obj)
    }
  }
}
