import {
  Component,
  OnInit,
  Input,
  OnChanges,
  Output,
  EventEmitter
} from '@angular/core';
import { PostViewModel } from '../model/post-view.model';
declare var $ : any;
@Component({selector: 'facebook', templateUrl: './facebook.component.html', styleUrls: ['./facebook.component.less']})
export class FacebookComponent implements OnInit,
OnChanges {
  @Input()fbPostData : PostViewModel;
  lastSlide : number = 0;
  constructor() {}

  ngOnInit() {
    $('.carousel').carousel({interval: false});

  }
  ngOnChanges() {}
  imageSlider(i) {
    if (this.lastSlide >= 0 && this.lastSlide < i) {
      $('.carousel').carousel("next");
      this.lastSlide = i;
    } else if (this.lastSlide >= 0 && this.lastSlide > i) {
      $('.carousel').carousel("prev");
      this.lastSlide = i;
    }

  }
  ngOnDestroy() {}
}
