export class CampaignViewModel{
    title: string;
    type: string;
    description: string;
    createdAt: Date;
    scheduledAt: Date;
    status: string;
    error: string;
    name: string;
    id: any;
    ownerId: string;
    createdOn: Date;
    updatedOn: Date;
    posts: Array<PostViewModel> = [];
    mediaFiles: Array<MediaFileAssetViewModel>;
    deletedPersistantImages: Array<any>;
    postsToDelete: Array<string>;
    campaignComments: Comments[];
    currentDate: Date;

    constructor() {
        this.title = '';
        this.description = '';
        this.currentDate = new Date();
        this.error = '';
        this.mediaFiles = [];
        this.id = '';
        this.name = '';
        this.deletedPersistantImages = [];
        this.campaignComments = [];
        this.posts = [];
        this.status = '';
        this.postsToDelete = [];
    }
}

