import { Component, OnInit, Input, OnChanges } from '@angular/core';
import { CommentsService } from '../service/comments.service';
// import { ActivatedRoute } from '@angular/router';
import {Socket} from 'ng-socket-io';
@Component({
  selector: 'campaign-comments-container',
  templateUrl: './campaign-comments-container.component.html',
  styleUrls: ['./campaign-comments-container.component.less']
})
export class CampaignCommentsContainerComponent implements OnInit , OnChanges {
  @Input() comments:Array<any>;
  @Input() campaignId:string;
  constructor(public commentsService:CommentsService,public socket:Socket) { }

  ngOnInit() {
  }
  ngOnChanges(){
    if(this.comments && this.comments.length > 0){
      for (let i = 0; i < this.comments.length; i++) {
        this.comments[i].children = [];
        this.comments[i].isChildrenShow = false;
        if (this.comments[i].parent) {
          for (let j = 0; j < this.comments.length; j++) {
            if (this.comments[i].parent === this.comments[j].id) {
  
              this.comments[j].children.push(this.comments[i]);
  
            }
          }
  
        }
      }
    }
    
  }
  sendComment = ($event) =>{
    this.commentsService.sendComment(this.campaignId, $event).subscribe(res => {
      this.sendMessageToIo();
    })
  }
  sendMessageToIo = () => {
    this.socket.emit("message", this.campaignId);
  }
  commentReply($event){
    this.commentsService.replyComment(this.campaignId, $event.msg, $event.p_id).subscribe(res => {
      this.sendComment($event.msg);
    })
  }
  showCommentReply($event){
    for (let i = 0; i < this.comments.length; i++) {

      if (this.comments[i].id == $event) {
        this.comments[i].isChildrenShow = !this.comments[i].isChildrenShow;

      }
    }
  }
}
