import { Component, OnInit, Input } from '@angular/core';
// import { ActivatedRoute } from '@angular/router';
import { PostService } from '../service/post-data.service';
import { PostViewModel, CampaignViewModel } from './model/post-view.model';
declare var $ : any;
import {Socket} from 'ng-socket-io';
@Component({
  selector: 'posts-preview-container',
  templateUrl: './posts-preview-container.component.html',
  styleUrls: ['./posts-preview-container.component.less']
})
export class PostsPreviewContainerComponent implements OnInit {
  posts:Array<PostViewModel>;
  orignalData:Array<CampaignViewModel>;
  campaignId:string;
  // accessToken:string;
  comments:Array<any>;
  @Input() accessToken:string;
  constructor(public postService : PostService,public socket:Socket) {
    this.posts = [];
    this.orignalData = [];
   }

  ngOnInit() {
    this.getMessage();
    // this.accessToken = this.activeroute.snapshot.params['id'];
    this
    .postService
    .getCampaignPosts(this.accessToken)
    .subscribe(res => {
      localStorage["exUserProfile"] = JSON.stringify(res.exUser)
      this.campaignId = res.campaign.id;
      if(res.campaign.campaignComments){
        this.comments = res.campaign.campaignComments;
      }
      if(res.campaign.posts){
        this.posts = res.campaign.posts;
        for(let i=0; i < this.posts.length;i++){
          this.posts[i].type = res.campaign.type;
        }
      }
      this.orignalData = res; 
    })
  }
  getMessage = () => {
    this.socket.on("message", (data) => {
      if(data.newCampaign.campaignComments){
        this.comments = data.newCampaign.campaignComments;
      }
      if(data.newCampaign.posts){
        this.posts = data.newCampaign.posts;
        for(let i=0; i < this.posts.length;i++){
          this.posts[i].type = data.newCampaign.type;
        }
      }
      console.log("get message from server using io",data)
    });
  }

}
